/* [9. Typeahead Js] =================================== */
.typeahead {
  width: calc(100% - 61px);
}
.typeahead a {
  padding: 10px 12px !important;
}
.typeahead > .active > a {
  background-color: #009688;
}
.typeahead > .active > a:hover {
  background-color: #009688;
}

