.alert.icon-alert.with-arrow i {
  position: relative;
}
.alert.icon-alert.with-arrow i:after {
  left: 100%;
  top: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-color: rgba(213, 36, 213, 0);
  border-width: 10px;
  margin-top: -9px;
