/* [12. Carousel] ===================================== */
.carousel .carousel-inner .carousel-caption h3 {
  color: #fff;
  font-size: 24px;
}
.owl-carousel .owl-item {
  overflow: hidden;
}
.owl-carousel .item {
  margin: 3px;
}
.owl-carousel .item img {
  display: block;
  width: 100%;
  height: auto;
}
.owl-carousel.styleOne .owl-nav {
  display: none;
}
.owl-carousel.styleTwo .owl-nav {
  color: #fff;
  font-size: 40px;
}
.owl-carousel.styleTwo .owl-nav .owl-prev {
  position: absolute;
  top: calc(50% - 42px);
  left: 8px;
}
.owl-carousel.styleTwo .owl-nav .owl-next {
  position: absolute;
  top: calc(50% - 42px);
  right: 8px;
}
.owl-carousel.styleTwo .owl-nav .fa {
  text-shadow: 0 0 5px #555;
}
.owl-carousel.styleThree {
  margin-top: 35px;
}
.owl-carousel.styleThree .owl-nav {
  color: #999;
  font-size: 34px;
  position: absolute;
  right: 0;
  top: -38px;
  padding-left: 10px;
  background-color: #fff;
  z-index: 9;
}
.owl-carousel.styleThree .owl-nav .owl-prev {
  float: left;
  margin-right: 8px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.owl-carousel.styleThree .owl-nav .owl-prev:hover {
  opacity: .7;
}
.owl-carousel.styleThree .owl-nav .owl-next {
  float: right;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.owl-carousel.styleThree .owl-nav .owl-next:hover {
  opacity: .7;
}
.styleThree.title {
  font-weight: bold;
  font-size: 16px;
  color: #6E7483;
  float: left;
}
.styleThree .owl-carousel-title {
  position: absolute;
  top: -30px;
  font-weight: bold;
  width: 100%;
  z-index: 3;
}
.styleThree .owl-carousel-title h3 {
  font-size: 15px;
  font-weight: bold;
  color: #6E7483;
  position: relative;
  background-color: #fff;
  width: auto;
  display: inline-block;
  padding-right: 10px;
  z-index: 3;
}
.styleThree .owl-carousel-title:after {
  content: '';
  width: 100%;
  border-bottom: solid 1px rgba(153, 153, 153, 0.4);
  position: absolute;
  left: 0;
  top: calc(25% + 2px);
  z-index: 1;
}
.owl-dots {
  text-align: center;
  margin-top: 6px;
}
.owl-dots .owl-dot {
  display: inline-block;
}
.owl-dots .owl-dot span {
  background: none repeat scroll 0 0 #ccc;
  border-radius: 20px;
  display: block;
  height: 12px;
  margin: 5px;
  width: 12px;
}
.owl-dots .owl-dot.active span {
  background-color: #6E7483;
}

/* [13. Content Slider] =============================== */
.slick-slider {
