/* [21. Timeline] ===================================== */
.timeline {
  list-style: none;
  padding: 0;
  margin-bottom: 0;
  position: relative;
}
.timeline:before {
  content: " ";
  top: 0;
  bottom: 0;
  position: absolute;
  width: 3px;
  background-color: #fff;
  left: 50%;
}
.timeline li {
  display: inline-block;
  margin-bottom: 50px;
  position: relative;
}
.timeline li:last-child {
  margin-bottom: -5px;
}
.timeline li .timeline-panel {
  position: relative;
  width: calc(50% - 50px);
  float: left;
  border: 1px solid #e5e5e5;
  border-radius: 3px;
}
.timeline li .timeline-panel:before, .timeline li .timeline-panel:after {
  left: 100%;
  top: 20px;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}
.timeline li .timeline-panel:before {
  border-color: rgba(229, 229, 229, 0);
  border-left-color: #e5e5e5;
  border-width: 11px;
  margin-top: -11px;
  margin-left: 1px;
}
.timeline li .timeline-panel:after {
  border-color: rgba(245, 246, 248, 0);
  border-left-color: #f5f6f8;
  border-width: 10px;
  margin-top: -10px;
}
.timeline li .timeline-panel .heading {
  background-color: #F5F6F8;
  border-bottom: 1px solid rgba(238, 238, 238, 0.6);
  padding: 10px 15px 8px 15px;
  border-radius: 3px 3px 0 0;
}
.timeline li .timeline-panel .heading h4 {
  margin: 0;
  font-weight: bold !important;
  font-size: 14px !important;
  color: #6E7483;
  display: inline-block;
}
.timeline li .timeline-panel .heading .time {
  margin-top: 5px;
  color: #aaa;
  font-size: 11px;
  font-weight: normal;
  cursor: default;
  display: inline-block;
  float: right;
  position: relative;
  bottom: 4px;
}
.timeline li .timeline-panel .body {
  background-color: #fff;
  padding: 10px 15px 15px 15px;
  font-size: 13px;
  color: #888;
  border-radius: 0 0 3px 3px;
}
.timeline li .timeline-badge {
  position: absolute;
  text-align: center;
  width: 50px;
  height: 50px;
  border: 3px solid #fff;
  left: 50%;
  top: -3px;
  font-size: 18px;
  line-height: 42px;
  margin-left: -23px;
  border-radius: 50%;
}
.timeline li .timeline-badge.success {
  background-color: #16a085;
  color: #fff;
}
.timeline li .timeline-badge.danger {
  background-color: #DA4453;
  color: #fff;
}
.timeline li .timeline-badge.primary {
  background-color: #2980b9;
  color: #fff;
}
.timeline li .timeline-badge.warning {
  background-color: #f6b225;
  color: #fff;
}
.timeline li.timeline-inverted .timeline-panel {
  float: right;
  position: relative;
}
.timeline li.timeline-inverted .timeline-panel:before, .timeline li.timeline-inverted .timeline-panel:after {
  left: -20px;
  top: 21px;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}
.timeline li.timeline-inverted .timeline-panel:after {
  border-color: rgba(245, 246, 248, 0);
  border-right-color: #f5f6f8;
  border-width: 10px;
  margin-top: -10px;
}
.timeline li.timeline-inverted .timeline-panel:before {
  border-color: rgba(229, 229, 229, 0);
  border-right-color: #e5e5e5;
  border-width: 11px;
  margin-top: -11px;
  margin-left: -3px;
}
@media (max-width: 1140px) {
.timeline:before {
    left: 40px;
}
.timeline .timeline-badge {
    left: 40px !important;
}
li:not(.timeline-inverted) .timeline-panel {
    width: calc(100% - 90px) !important;
    margin-left: 90px;
    position: relative;
}
li:not(.timeline-inverted) .timeline-panel:before, li:not(.timeline-inverted) .timeline-panel:after {
    left: -20px;
    top: 21px;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
}
li:not(.timeline-inverted) .timeline-panel:after {
    border-color: rgba(245, 246, 248, 0);
    border-right-color: #f5f6f8;
    border-width: 10px;
    margin-top: -10px;
}
li:not(.timeline-inverted) .timeline-panel:before {
    border-color: rgba(229, 229, 229, 0);
    border-right-color: #e5e5e5;
    border-width: 11px;
    margin-top: -11px;
    margin-left: -3px;
}
li.timeline-inverted .timeline-panel {
    width: calc(100% - 90px) !important;
}
}
.timeline-left-version.timeline:before {
  left: 40px;
}
.timeline-left-version.timeline .timeline-badge {
  left: 40px !important;
}
.timeline-left-version li:not(.timeline-inverted) .timeline-panel {
  width: calc(100% - 90px) !important;
  margin-left: 90px;
  position: relative;
}
.timeline-left-version li:not(.timeline-inverted) .timeline-panel:before, .timeline-left-version li:not(.timeline-inverted) .timeline-panel:after {
  left: -20px;
  top: 21px;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}
.timeline-left-version li:not(.timeline-inverted) .timeline-panel:after {
  border-color: rgba(245, 246, 248, 0);
  border-right-color: #f5f6f8;
  border-width: 10px;
  margin-top: -10px;
}
.timeline-left-version li:not(.timeline-inverted) .timeline-panel:before {
  border-color: rgba(229, 229, 229, 0);
  border-right-color: #e5e5e5;
  border-width: 11px;
  margin-top: -11px;
  margin-left: -3px;
}
.timeline-left-version li.timeline-inverted .timeline-panel {
  width: calc(100% - 90px) !important;
}

